import * as React from 'react';
import { Checkbox, List } from 'antd-mobile';
import { IProps, IState, IUser, IOrg } from '../type';
import SelectUserItem from './item';
import { observer, inject } from 'mobx-react';
import { toJS } from 'mobx';

interface ISelectUserListProps {
    model: any;
    searchText: string;
}

const SelectUserList = observer((props: ISelectUserListProps) => {
    const {
        searchText,
        model: { persons, usersMap, onSelectChange, allCheckBox },
    } = props;
    return (
        <>
            {persons && persons.length ? (
                <Checkbox.CheckboxItem onChange={allCheckBox}>
                    全选
                </Checkbox.CheckboxItem>
            ) : null}
            {Array.isArray(persons) &&
                persons
                    .filter(
                        (item) =>
                            !searchText ||
                            (item.name && item.name.indexOf(searchText) !== -1)
                    )
                    .map((user: IUser, index: number) => {
                        return (
                            <SelectUserItem
                                key={user.memberId}
                                index={index}
                                rowData={user}
                                checked={usersMap.has(user.memberId)}
                                onChange={onSelectChange}
                            />
                        );
                    })}
        </>
    );
});

export default SelectUserList;
