import * as React from "react";
import { Checkbox, List } from "antd-mobile";
import { IProps, IState, IUser, IOrg } from "../type";
import { observer, inject } from "mobx-react";
const CheckboxItem = Checkbox.CheckboxItem;
type IDepartmentItemProps = {
  model: IOrg;
  onClick: Function;
};

@observer
export default class SelectUserItem extends React.Component<
  IDepartmentItemProps
> {
  departmentHandle = () => {
    this.props.onClick(this.props.model);
  };

  render() {
    const { total, orgName } = this.props.model;

    return (
      <List.Item arrow="horizontal" onClick={this.departmentHandle}>
        {orgName} {`(${total}人)`}
      </List.Item>
    );
  }
}
