require("./style.scss");
import React from "react";
import { observer, inject } from "mobx-react";
import { RightOutlined } from "@ant-design/icons";
import { IOrg } from "../type";
import classnames from "classnames";
type IProps = {
  model: any;
};

@observer
export default class SlideTree extends React.Component<IProps> {
  slideHandle = (index: number) => {
    this.props.model.setBreadcrumbsSlice(index);
  };

  render() {
    const { breadcrumbs } = this.props.model;
    return (
      <div className="slide_tree">
        {breadcrumbs.map((item: IOrg, index: number) => {
          return (
            <div key={index} className="slide_tree_item">
              {index === 0 ? null : (
                <div className={classnames("slide_tree_separator")}>
                  <RightOutlined
                    style={{
                      color:
                        breadcrumbs.length - 1 === index
                          ? "#666666"
                          : "#0d8df9",
                    }}
                  />
                </div>
              )}
              <div
                onClick={() => {
                  this.slideHandle(index);
                }}
                className={classnames("slide_tree_title", {
                  slideTreeBlue: breadcrumbs.length - 1 !== index,
                  slideTreeCCC: breadcrumbs.length - 1 === index,
                })}
              >
                {item.orgName}
              </div>
            </div>
          );
        })}
      </div>
    );
  }
}
