import React, { useEffect } from "react";
import { NavBar, Icon } from "antd-mobile";
import classNames from "classnames";
require("./style.scss");

const DEFAULT_BG_COLOR = "#fff";

const SubPage = (props: any) => {
  const {
    title,
    className,
    bgColor,
    rightContent,
    children,
    history,
    prePage,
    isAccessory // 是否附属
  } = props;

  useEffect(() => {
    document.body.style.backgroundColor = bgColor;
    return () => {
      document.body.style.backgroundColor = DEFAULT_BG_COLOR;
    };
  }, []);
  return (
    <>
      <NavBar
        className="base-navBar"
        mode="dark"
        icon={<Icon size="lg" type="left" />}
        onLeftClick={() => {
          if (prePage && isAccessory) {
            history.replace({
              pathname: prePage,
              state: {
                pathname: location.pathname
              }
            });
          } else if (prePage && history.length === 1) {
            history.push({ pathname: prePage });
          } else {
            // window.location.replace('/dashboard');
            history.goBack();
          }
        }}
        rightContent={rightContent}
      >
        {title}
      </NavBar>
      <div className={classNames("base-content", className)}>{children}</div>
    </>
  );
};

export default SubPage;
