import React from "react";
import { observer, inject } from "mobx-react";
import { toJS } from "mobx";
require("./style.scss");
import SlideTree from "@/pages/choosePerson/SlideTree";
import SelectedText from "@/pages/choosePerson/SelectedText";
import SelectUserList from "./SelectUserList";
import DepartmentList from "./DepartmentList";
import { NavBar, Icon, Button, List, WhiteSpace, SearchBar, Toast } from "antd-mobile";
import { IProps, IState, IUser, IOrg } from "./type";
import storage from "@/utils/storage";

@inject((stores: any) => ({
  store: stores.choosePersonStore
}))
@observer
class ChoosePerson extends React.Component<IProps, IState> {
  listRef: any;
  height: number;
  constructor(props: IProps) {
    super(props);
    this.state = { height: 0 };
    this.listRef = React.createRef();
  }
  componentDidMount() {
    const selectedPerson = storage.getItem("selectedPerson") ? JSON.parse(storage.getItem("selectedPerson")) : null;
    if (selectedPerson) {
      // storage.removeItem("selectedPerson");
    }
    const state = selectedPerson || this.props.location.state;
    const { initStore, onSlideTreeChange, breadcrumbs } = this.props.store;
    if (state) {
      initStore(state);
    }
    if (state.pathname === "/remove_person") {
      onSlideTreeChange({
        orgId: breadcrumbs[breadcrumbs.length - 1].orgId
      });
    } else {
      onSlideTreeChange({ orgId: breadcrumbs[0].orgId });
    }
    this.setState({
      height: document.documentElement.clientHeight - this.listRef.current.offsetTop
    });
  }
  componentWillUnmount() {
    this.props.store.reset();
  }
  onSlideTreeChange = (breadcrumbs: Array<IOrg>) => {
    //去刷新接口取数组最后一个id
    const { onSlideTreeChange } = this.props.store;
    onSlideTreeChange(breadcrumbs[breadcrumbs.length - 1]);
  };
  onSelectedChange = (users: Array<IUser>) => {
    const { setSelected } = this.props.store;
    setSelected(users);
  };
  onLeftClick = () => {
    this.props.history.goBack();
  };
  onSubmit = () => {
    this.props.store.onSubmit((pathname: string, selected: Array<any>) => {
      const state = {
        pathname: "/choose_person",
        selected: toJS(
          selected.filter(item => item.memberId),
          { recurseEverything: true }
        )
      };
      // this.props.history.push({
      //   pathname,
      //   state,
      // });
      this.props.history.goBack();
      storage.setItem("meetingPerson", JSON.stringify(state.selected));
    });
  };
  departmentHandle = (row: IOrg) => {
    //Todo去刷新接口
    const { setBreadcrumbsPush } = this.props.store;
    setBreadcrumbsPush(row);
  };
  onSelectChange = (rowData: IUser) => {
    const { onSelectChange } = this.props.store;
    onSelectChange(rowData);
  };
  onSearchSubmit = (value: string) => {
    //去调用搜索接口
    if (!value.trim()) {
      return Toast.success("搜索内容不能为空");
    }
    const {
      store: { onSearchSubmit }
    } = this.props;
    onSearchSubmit(value);
  };

  render() {
    const { pageTitle, searchText, setSearchText, searchValue, setSearchValue } = this.props.store;
    const { height } = this.state;

    return (
      <div className="choosePerson-page">
        <NavBar
          mode="dark"
          icon={<Icon type="left" />}
          onLeftClick={this.onLeftClick}
          rightContent={
            <a
              onClick={e => {
                e.preventDefault();
                this.onSubmit();
              }}
              style={{ marginRight: "5px" }}
            >
              确定
            </a>
          }
        >
          {pageTitle}
        </NavBar>
        <SelectedText {...this.props} model={this.props.store} />

        <SearchBar
          placeholder="在结果中搜索"
          className="choose_person_search"
          value={searchValue}
          onChange={setSearchValue}
          onSubmit={setSearchText}
          onCancel={() => {
            setSearchText("");
            setSearchValue("");
          }}
          onClear={() => {
            setSearchText("");
            setSearchValue("");
          }}
        />

        <SlideTree model={this.props.store} />
        <WhiteSpace />
        <div ref={this.listRef}>
          <List
            style={{
              height: height || 0,
              overflow: "auto"
            }}
          >
            <DepartmentList model={this.props.store} />
            <WhiteSpace />
            <SelectUserList model={this.props.store} searchText={searchText} />
          </List>
        </div>
      </div>
    );
  }
}
export default ChoosePerson;
