import * as React from "react";
import { Checkbox, List } from "antd-mobile";
import { observer, inject } from "mobx-react";
import { IProps, IState, IUser, IOrg } from "../type";
import DepartmentItem from "./item";
const CheckboxItem = Checkbox.CheckboxItem;
interface IDepartmentListProps {
  model: any;
}

@observer
export default class DepartmentList extends React.Component<
  IDepartmentListProps
> {
  render() {
    const { departments, setBreadcrumbsPush } = this.props.model;

    return (
      <>
        {departments.map((item: IOrg) => (
          <DepartmentItem
            key={item.orgId}
            onClick={setBreadcrumbsPush}
            model={item}
          />
        ))}
      </>
    );
  }
}
