require("./style.scss");
import React from "react";
import { UserOutlined, RightOutlined } from "@ant-design/icons";
import { observer, inject } from "mobx-react";
import { toJS } from "mobx";

type IProps = {
  history: any;
  model: any;
};

const SelectedText = observer((props: IProps) => {
  const state = props.history.location;
  if (state) {
    const { pathname, users } = state;
    if (pathname === "/remove_person") {
      props.model.setSelected(users);
    }
  }

  const onSelectedRemove = () => {
    const { selected } = props.model;

    const state = {
      pathname: "/choose_person",
      dataSource: toJS(selected, {
        recurseEverything: true,
      }),
    };

    props.history.push({
      pathname: "/remove_person",
      state,
    });
  };
  const { selectedTitle } = props.model;
  return (
    <>
      <div className="selected_text" onClick={onSelectedRemove}>
        <UserOutlined />
        <span className="selected_text_icon">已选择:</span>
        <span className="selected_text_title">{selectedTitle}</span>
        <div className="selected_text_right">
          <RightOutlined />
        </div>
      </div>
    </>
  );
});

export default SelectedText;
