import * as React from "react";
import { Checkbox, List } from "antd-mobile";
import { observer, inject } from "mobx-react";
const CheckboxItem = Checkbox.CheckboxItem;
interface ISelectUserItemProps {
  rowData: any;
  onChange: any;
  index: number;
  checked: boolean;
}

const SelectUserItem = observer((props: ISelectUserItemProps) => {
  const onChange = (e: any) => {
    props.onChange(props.rowData);
  };

  const {
    rowData: { memberId, workNumber, name, position },
    checked,
  } = props;

  return (
    <CheckboxItem checked={checked} onChange={onChange} disabled={!workNumber}>
      {name} ({position}、{workNumber})
    </CheckboxItem>
  );
});

export default SelectUserItem;
