import React from 'react';
import { Tabs, Tag } from 'antd-mobile';
import SubPage from '@/components/SubPage';
require('./style.scss');

class State extends React.Component<IProps, GState> {
    render() {
        const {
            history,
            location: { state = {} },
        } = this.props;
        const { checkedIn = [], notCheckedIn = [] } = state;
        const tabs = [
            { title: `已签到(${checkedIn.length}人)` },
            { title: `未签到(${notCheckedIn.length}人)` },
        ];

        return (
            <SubPage
                history={history}
                className="choosePerson-state-page"
                title="签到信息"
                bgColor="#eee"
            >
                <Tabs
                    tabs={tabs}
                    initialPage={0}
                    animated={false}
                    useOnPan={false}
                    swipeable={false}
                >
                    <div className="choosePerson-state-tab">
                        {checkedIn.map((item: any, i: number) => (
                            <div
                                className="choosePerson-state-item"
                                key={item.confereeId || i}
                            >
                                {item.confereeName}
                                <span>{`(${item.workNumber})`}</span>
                            </div>
                        ))}
                    </div>
                    <div className="choosePerson-state-tab">
                        {notCheckedIn.map((item: any, i: number) => (
                            <div
                                className="choosePerson-state-item"
                                key={item.confereeId || i}
                            >
                                {item.confereeName}
                                <span>{`(${item.workNumber})`}</span>
                            </div>
                        ))}
                    </div>
                </Tabs>
            </SubPage>
        );
    }
}

export default State;
